import '../Post.scss';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { Image, Alert } from 'react-bootstrap';
import React, { useEffect } from 'react';

const info = {
  name: "Fundamentals of 3D Web Visualizations with Three.js",
  date: "Feb 3 2021",
  tags: ["python"]
};

const codeBlock1 = `
pip install some-package
`.trim();

export default function Post() {

  return (
    <div>
      <h3>{info.name}</h3>
      <p className="dateText">{info.date}</p> 
      <p>
        Three.js is a powerful library for developing 3D web visualizations. Let's take
        a look at the fundaments of using the library to get an understanding of how
        we can start building web visualizations today.
      </p>
      <Alert variant="info">
        If you're using a web framework like React, you'd want to npm install three 
        and import things from the three module. Instead, I'm using the regular old 
        version that adds everything to a global variable called THREE, which is why 
        you'll see a big THREE prefix in the JavaScript code.
      </Alert>
      <h4>What are we going to build?</h4>
      <p style={{marginBottom: 0}}>
        Here's a JSFiddle with the project we'll be building. It's a rotating
        sphere comprised of dots with a background of stars. Click on the Result tab
        to see the final result.        
      </p>
      <iframe width="100%" height="600" src="//jsfiddle.net/Bryce93/Lzcpqsf1/143/embedded/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
      <p>
        To give credit where credit is due, motivation for this post and the approach to
        dot generation for the sphere comes from GitHub and Stripe. Check out These
        blog posts for more information.
      </p>
      <ul>
        <li>
          <a href="https://stripe.com/blog/globe">https://stripe.com/blog/globe</a>
        </li>
        <li>
          <a href="https://github.blog/2020-12-21-how-we-built-the-github-globe/">https://github.blog/2020-12-21-how-we-built-the-github-globe/</a>
        </li>
      </ul>
      <p>
        Below, we'll go over the fundamentals of Three.js visualizations. I encourage you
        to reference the example to see how these concepts are implemented in practice.
      </p>
      <h4>The Basic Framework</h4>
      <p>
        Every Three.js scenario includes a couple of things:
      </p>
      <ul>
        <li>
          <b>A scene.</b> Scenes allow you to set up what and where is to be rendered by 
          three.js. This is where you place objects, lights and cameras.
          (<a href="https://threejs.org/docs/#api/en/scenes/Scene">documentation</a>)
        </li>
        <li>
         <b>A camera</b> The most common type is a perspective camera. There are a couple
          of parameters to know, such as fov, near, and far. 
          (<a href="https://threejs.org/docs/#api/en/cameras/PerspectiveCamera">documentation</a>)
          (<a href="https://threejsfundamentals.org/threejs/lessons/threejs-cameras.html">examples</a>)
        </li>
        <li>
          <b>A renderer.</b> This is what displays your scene using WebGL. Antialias and logarithmic
          depth buffer attributes can be set here. Antialias makes your application less 
          jagged, but at a performance cost. Using a logarithmic depth buffer is great for
          space visualiztions where there's a large scale at play.
          (<a href="https://threejs.org/docs/#api/en/renderers/WebGLRenderer">documentation</a>)
        </li>
        <li>
          <b>A render loop.</b> This is the function that gets called between each frame. In
          our example, we called this function animate. The requestAnimationframe function
          ensures it gets called again on subsequent renders, and the render function is what 
          actually performs the drawing. You can add additional logic here for moving things
          around, like the camera or the globe.
        </li>
        <li>
          <b>A resize function.</b> This isn't strictly required, but it's nice to have
          in most cases. If your viewport changes, Three.js won't automatically know to 
          change the scene. You'll want a resize function to tell Three.js to change
          the renderer size and camera aspect when this occurs.
        </li>
      </ul>
      <h4>Adding Objects</h4>
      <p>
        Every object we add to our Three.js scene includes a couple of things:
      </p>
      <ul>
        <li>
          <b>A geometry.</b> There's a geometry for just about every shape. This is what
          describes the wireframe, or general shape and size of your object. Here's the
          documentation for the BoxGeometry for reference: <a href="https://threejs.org/docs/#api/en/geometries/BoxGeometry">https://threejs.org/docs/#api/en/geometries/BoxGeometry</a>.
        </li>
        <li>
          <b>A material.</b> If the geometry is the poles to the tent, then this is the tarp.
          The material describes the appearance of objects. This includes color, how light 
          affects it, and more. Here's the documentation for the MeshBasicMaterial for
          reference: <a href="https://threejs.org/docs/#api/en/materials/MeshBasicMaterial">https://threejs.org/docs/#api/en/materials/MeshBasicMaterial</a>.
        </li>
        <li>
          <b>A mesh.</b> This is what ties it all together. A mesh includes a geometry and 
          a material. This is what you add to your scene. You can also add a mesh to other
          meshes, and it will automatically reflect the position and rotation of that parent object.
        </li>
      </ul>
      <h4>Tying it all together</h4>
      <p>
        In our example, we have one sphere and two point cloud objects. We manage
        animation by either modifying objects within our render loop, or using the 
        tween.js library to modify these values for us. We can modify anything, including
        position, rotation, color, scale, and more. In this example, we modify the 
        globe scale with a TWEEN animation when we first load the application, and 
        we modify the globe and star rotation while animating the scene.
      </p>
      <p>
        You may have noticed that we don't have any lights in this example. How lights affect 
        an object depend on that object's material. The MeshBasicMaterial and PointsMaterial aren't 
        impacted by lighting, so we didn't need it in this example. If you're interested
        in lighting, try changing the sphere to a MeshPhongMaterial and adding some 
        lights. Some good lights to try are the <a href="https://threejs.org/docs/#api/en/lights/AmbientLight">AmbientLight</a> and
        the <a href="https://threejs.org/docs/#api/en/lights/DirectionalLight">DirectionalLight</a>. Note
        that with the directional light, we'll want to change it's position with something
        like <code>myDirectionalLight.position.set(x, y, z)</code>.
      </p>
      <h4>Next Steps</h4>
      <ul>
        <li>
          Check out <a href="https://threejs.org/">https://threejs.org/</a>. It has 
          tons of examples and fantastic documentation.
        </li>
        <li>
          Lighting, more geometries, more meshes, and camera controllers.
        </li>
        <li>
          <a href="https://discoverthreejs.com/tips-and-tricks/">The Big List of three.js Tips and Tricks!</a>
        </li>
      </ul>
    </div>
  )
}